import { InputBase, TextField } from "@material-ui/core";
import { useState } from "react";
import { NavLink } from "react-router-dom";
import SearchIcon from '@material-ui/icons/Search';
import "./Search.css";

function Search({data}): JSX.Element {
    const [inputValues ,setInputValues] = useState([])
    const filterHandler =((event)=>{
        const searchWord = event.target.value;
         const newFilter =data.filter((value)=>{
            return value.name.includes(searchWord)
        })
        if(searchWord === ''){
            setInputValues([])
        }
        else{
            setInputValues(newFilter)
        }
      
    })
    return (
        <div className="Search">
            <TextField variant="filled" placeholder="חיפוש מוצר" onChange={filterHandler} onKeyPress={filterHandler} type="search" />
           
            { inputValues.length != 0 &&(
                <div className="data">
                    {inputValues.slice(0,10).map(p => <a key={p.id} href={"/products/buy/" + p.image + "&&" +p.id}><p key={p.id}>{p.name}</p></a>)}
                </div>
            )}
        </div>

    );
}

export default Search;
